//Components
import Event from '../../Components/Event';
import Results from '../../Components/Results';

import VideoPlayer from '../../Components/VideoPlayer';
import { useEffect, useState} from 'react';
import './Dashboard.css'
import '../../styles/clock.css'
import '../../styles/arena.css'
import '../../styles/customs.css'


const Matches = [
  {
  match: 1,
  win: 'meron',
  start: 1,
  lastCall: 75,
  endBet: 89,
  endMatch: 152
  },
  {
    match: 2,
    win: 'wala',
    start: 190,
    lastCall: 230,
    endBet: 243,
    endMatch: 284
    },
    {
      match: 3,
      win: 'meron',
      start: 310,
      lastCall: 350,
      endBet: 369,
      endMatch: 420
      }
];

function getRandomInt(min, max) {
  min = Math.ceil(min);
  max = Math.floor(max);
  return Math.floor(Math.random() * (max - min) + min); // The maximum is exclusive and the minimum is inclusive
}


function App() {
  const [currentMatch, setCurrentMatch] = useState({});
  const [bet, setBet] = useState(0);
  const [currentBet, setCurrentBet] = useState({
    match: null,
    side: '',
    amount: 0
  });
  const [lastMatch, setLastMatch] = useState({});
  const [timelapse, setTimelapse] = useState(0)
  const [balance, setBalance] = useState(0)
  const [lastCall, setLastCall] = useState(false)
  const [betting, setBetting] = useState(false)
  const [dateState, setDateState] = useState(new Date());
  const [meron, setMeron] = useState(0);
  const [wala, setWala] = useState(0);
  const [isReset, setIsReset] = useState(false);
  const [maxBets, setMaxBets] = useState({
    wala: 500000,
    meron: 500000
  })
  const [rnd, setRnd] = useState(0);

  const handleChange = (e) => {
    setBet(e)
  }


  const handleBet = (val) => {
    if(bet === 0) return alert('Enter Amount')
          let newBet = val === 'meron' ? Number(meron) + Number(bet) : Number(wala) + Number(bet);
          let newBal = Number(balance) - Number(bet);
            if(val === 'meron'){
              setMeron(newBet)
              setCurrentBet({
                match: currentMatch.match,
                side: 'meron',
                amount: Number(currentBet.amount) + Number(bet)
              })
              localStorage.setItem('meron', newBet);
              localStorage.setItem('bet', JSON.stringify({
                match: currentMatch.match,
                side: 'meron',
                amount: Number(currentBet.amount) + Number(bet)
              }));
            } else {
              setWala(newBet)
              setCurrentBet({
                match: currentMatch.match,
                side: 'wala',
                amount: Number(currentBet.amount) + Number(bet)
              })
              localStorage.setItem('wala', newBet);
              localStorage.setItem('bet', JSON.stringify({
                match: currentMatch.match,
                side: 'wala',
                amount: Number(currentBet.amount) + Number(bet)
              }));
            }
          setBalance(newBal)
  }

  const handleReset = () => {
    localStorage.setItem('meron', 0);
    localStorage.setItem('wala', 0);
    localStorage.setItem('timenow', 0);
    setMaxBets({
      wala: getRandomInt(100000, 500000),
      meron: getRandomInt(100000, 500000)
    })
    setIsReset(true);
    setRnd(0)
  }

  const handleResetBet = () => {
    setMaxBets({
      wala: getRandomInt(100000, 500000),
      meron: getRandomInt(100000, 500000)
    })
    localStorage.setItem('meron', 0);
    localStorage.setItem('wala', 0);
    setIsReset(true);
    setRnd(0)

   
  }

  useEffect(() => {
    let mrn = localStorage.getItem('meron');
    let wla = localStorage.getItem('wala');
    let ind = Matches.find(a => a.start <= timelapse && a.endMatch >= timelapse);

      let resetBet = Matches.find(a => (a.endMatch <= timelapse && (a.endMatch + 1) >= timelapse));
      
      if(resetBet && !isReset){
        setLastMatch(resetBet)
        setCurrentMatch({});
        setCurrentBet({
          match: null,
          side: '',
          amount: 0
        })
          setBalance(Number(balance) + 500000);
          localStorage.setItem('balance', Number(balance) + 500000)
         localStorage.setItem('meron', 0);
         localStorage.setItem('wala', 0);
         localStorage.removeItem('bet')
         setMeron(0)
         setWala(0)
         setRnd(0)
        setIsReset(true)
      }

      if(ind){
        setCurrentMatch(ind)
        localStorage.setItem('match', JSON.stringify(currentMatch));

        let onBet = ind.start < timelapse && ind.endBet > timelapse;
      let lastC = ind.lastCall < timelapse && ind.endBet > timelapse;
     
    
      if(onBet){
        setBetting(true)
      if(!isReset){

      if(mrn < maxBets.meron){
        let betMrn = Number(mrn) + getRandomInt(100, 3000)
        setMeron(betMrn)
        localStorage.setItem('meron', betMrn);
      }
      if(wla < maxBets.wala){
        let betWla = Number(wla) + getRandomInt(100, 3000)
        localStorage.setItem('wala', betWla);
        setWala(betWla)
      } 
      setIsReset(false)
    } else {
      localStorage.setItem('meron', 0);
      localStorage.setItem('wala', 0);
      setMeron(0)
      setWala(0)
      setRnd(0)
      setIsReset(false)
    }
  } else {
    localStorage.setItem('meron', mrn);
    localStorage.setItem('wala', wla);
    setMeron(mrn)
    setWala(wla)
    setBetting(false)

  }
  setLastCall(lastC)
  } else {
    setCurrentMatch({})
    localStorage.removeItem('match');
  }

   setRnd(Math.random())
}, [dateState]);

  useEffect(() => {
    let bal = localStorage.getItem('balance');
  

    setBalance(bal ? bal : 0);
         setInterval(() => {
          setDateState(new Date())
          //  localStorage.setItem('meron', meron);
          //  localStorage.setItem('wala', wala);
        }, 1000);
  }, []);

  useEffect(() => {
    let currentBet = localStorage.getItem('bet');

    if(currentBet){
      setCurrentBet(JSON.parse(currentBet))
    }
    if(currentMatch.match){
      let ind = Matches.map(a => { return a.match}).indexOf(currentMatch.match);
      if(ind !== -1){
        setLastMatch(Matches[ind])
      }
    }
  
  }, [currentMatch])

  return (
    <>
      <div style={{ textAlign: 'right' }} className="bettingBgColor">
       
      <a href="/sabong/" className="btn btn-primary btn-sm m-1">
        <i className="fa fa-home" title="HOME" />
      </a>
      <a
        className="btn btn-primary btn-sm m-1"
        title="RERORTS"
      >
        <i className="fa fa-area-chart" />
      </a>
      <a href="#" className="btn btn-primary btn-sm m-1">
        WITHDRAWAL
      </a>
    </div>
    <div className="wrapper">
    <div className="liveStream">
        <Event dateState={dateState}/>
      <VideoPlayer
        timelapse={timelapse}
        setTimelapse={setTimelapse}
        matchData={Matches}
        handleReset={() => handleReset()}
      />
      {/* {betting ? <h3>Start Betting</h3> : <h3>Betting Ended!</h3>}
      {lastCall && <h3>Last Call</h3>} */}


    </div>
    <div className='play'>
    <div className="bettingStatus blink_me">
        Payout with 120 and below shall be cancelled
      </div>
      <table className="currentGame">
        <tbody>
          <tr>
            <th>Betting</th>
            <th>
              Fight #{' '}
              { lastMatch.match && <span id="lblWinnerFight" className={ lastMatch.win === 'meron' ? "meronresult" :  lastMatch.win === 'wala' ? "walaresult" : "" }>
                {`${lastMatch.match}: ${String(lastMatch.win).toUpperCase()}`}
              </span> }
            </th>
          </tr>
          <tr>
            <td>
              <span>
                <span
                  className={betting ? lastCall ? "text-last-bet" : "text-open-bet" : "text-closed-bet"}
                  data-name="close"
                  id="lblStatus"
                >
                  {betting ? lastCall ? 'LAST CALL' : 'OPEN' : 'CLOSE'}
                </span>
              </span>
            </td>
            <td>
              <span id="lblGameInfoGameNumber" className="text-fightnumber">
                {currentMatch.match}
              </span>
            </td>
          </tr>
        </tbody>
      </table>
      <div className="bets">
      <div className="betMeron">
        <div className="title">MERON</div>
        <div className="info">
          <div className="totalBets">
            <span id="tdTotalBetMeron-1" className="">{meron}</span>
          </div>
          <div className="payout">
            PAYOUT = <span id="tdPayOutMeron">220.1</span>
          </div>
          <div className="currentBet">
            <span id="tdPersonalBetMeron-1">{currentBet.side === 'meron' ? currentBet.amount : 0}</span>
            <span> = </span>
            <span id="lblMeronWinning" className="bet-amount bet-win">0</span>
          </div>
          <button
            type="button"
            className="btn btn-success  btn-block    btn-lg "
            id="btnMeronBet"
            value="MERONBET"
            title="MERON"
            onClick={() => handleBet('meron')}
            disabled={!betting}
          >
            <i className="fa fa-plus-circle  "></i> BET MERON
          </button>
        </div>
      </div>
      <div className="betWala">
        <div className="title">WALA</div>
        <div className="info">
          <div className="totalBets">
            <span id="tdTotalBetWala-1" className="">{wala}</span>
          </div>
          <div className="payout">
            PAYOUT = <span id="tdPayOutWala">150.6</span>
          </div>
          <div className="currentBet">
            <span id="tdPersonalBetWala-1">{currentBet.side === 'wala' ? currentBet.amount : 0}</span>
            <span> = </span>
            <span id="lblWalaWinning" className="bet-amount bet-win">0</span>
          </div>
          <button
            type="button"
            className="btn btn-success btn-block   btn-lg "
            data-id="1"
            data-name="W"
            id="btnWalaBet-1"
            onClick={() => handleBet('wala')}
            disabled={!betting}
          >
            <i className="fa fa-plus-circle  "></i> BET WALA
          </button>
        </div>
      </div>
    </div>
    <div className="currentPoints">
      <p>Current Points :</p>
      <p>
        <i className="fa fa-money"></i>{' '}
        <span
          id="lblTotalPointsDisplay"
          className="title-background-gradient"
        >{Number(balance).toFixed(2)}</span>
      </p>
    </div>
    <div className="amount">
      <input
        type="text"
        value={bet}
        id="txtBetAmountZero"
        className="input-lg textColorBet text-center form-control"
        text-center=""
        placeholder="ENTER AMOUNT"
        // onKeyPress={() => isNumber(event)}
        onChange={(e) => handleChange(e.target.value)}
      />
      <button
        className="clearBtn"
        type="button"
        value=""
        // onClick={resetManualBet}
      >
        CLEAR
      </button>
    </div>
    <div className="amountsBet">
      <button
        className="btn btn-success btn-outline btn-xs quickbet mr-1"
        id="btnManual10k"
        onClick={() => setBet(10)}
      >
        10
      </button>
      <button
        className="btn btn-success btn-outline btn-xs quickbet mr-1"
        id="btnManual300"
        onClick={() => setBet(100)}

      >
        100
      </button>
      <button
        className="btn btn-success btn-outline btn-xs quickbet mr-1"
        id="btnManual100"
        onClick={() => setBet(500)}

      >
        500
      </button>
      <button
        className="btn btn-success btn-outline btn-xs quickbet mr-1"
        id="btnManual500"
        onClick={() => setBet(1000)}
      >
        1k
      </button>
      <button
        className="btn btn-success btn-outline btn-xs quickbet mr-1"
        id="btnManual1000"
        onClick={() => setBet(2000)}
      >
        2k
      </button>
      <button
        className="btn btn-success btn-outline btn-xs quickbet mr-1"
        id="btnManual2000"
        onClick={() => setBet(5000)}
      >
        5k
      </button>
      <button
        className="btn btn-success btn-outline btn-xs quickbet mr-1"
        id="btnManual1000"
        onClick={() => setBet(10000)}
      >
        10k
      </button>
    </div>
    <div className="betDraw ">
<div className="drawedTotal">
TOTAL DRAW:
</div>
<div className="drawed">
<span id="tdTotalBetDraw-8">0</span>
</div>
</div>
<div className="betDraw ">
<button type="button" className="btn btn-success btn-block   btn-lg " data-id="8" data-name="D" id="btnDrawBet" value="DRAW" title="DRAW"
onClick={() => handleResetBet()}
>
<i className="fa fa-plus-circle  "></i> BET DRAW
</button>
<div className="drawed">
<span id="tdPersonalBetDraw-8">
0
</span>
</div>
</div>
<div className="note">
<i className="fas fa-dice-d6"></i>
DRAW WINS X 8. Max. DRAW bet 1000/fight
</div>
<Results/>
    </div>
     {/* <div className="bets">
      <div className="betMeron">
        <div className="title">MERON</div>
        <div className="info">
          <div className="totalBets">
            <span id="tdTotalBetMeron-1" className="">21,862</span>
          </div>
          <div className="payout">
            PAYOUT = <span id="tdPayOutMeron">220.1</span>
          </div>
          <div className="currentBet">
            <span id="tdPersonalBetMeron-1">0</span>
            <span> = </span>
            <span id="lblMeronWinning" className="bet-amount bet-win">0</span>
          </div>
          <button
            type="button"
            className="btn   btn-success  btn-block    btn-lg "
            data-id="1"
            data-name="M"
            id="btnMeronBet-1"
            value="MERONBET"
            title="MERON 10-10"
          >
            <i className="fa fa-plus-circle  "></i> BET MERON
          </button>
        </div>
      </div>
      <div className="betWala">
        <div className="title">WALA</div>
        <div className="info">
          <div className="totalBets">
            <span id="tdTotalBetWala-1" className="">31,958</span>
          </div>
          <div className="payout">
            PAYOUT = <span id="tdPayOutWala">150.6</span>
          </div>
          <div className="currentBet">
            <span id="tdPersonalOdd-1" className="hidden">
              10-10
            </span>
            <span id="tdPersonalOddId-1" className="hidden">1</span>
            <span id="tdPersonalBetWala-1">0</span>
            <span> = </span>
            <span id="lblWalaWinning" className="bet-amount bet-win">0</span>
          </div>
          <button
            type="button"
            className="btn btn-success btn-block   btn-lg "
            data-id="1"
            data-name="W"
            id="btnWalaBet-1"
            value="WALABET"
            title="WALA 10-10"
          >
            <i className="fa fa-plus-circle  "></i> BET WALA
          </button>
        </div>
      </div>
    </div>  */}
    </div>
    </>

  );
}

export default App;
