import { BrowserRouter } from "react-router-dom"
import Routes from "./Routes";
import './App.css'
import './index.css';


function App() {
  
  return (
    <BrowserRouter basename="/sabong">
 <main>
        {/* You can use by useRoutes like this (I prefer it): */}
        {/* {routeResult} */}
        {/* Or use by defining it */}
        <Routes />
      </main>
      </BrowserRouter>
  );
}

export default App;
