import React, { useEffect, useState } from 'react'

//Components
import Navigation from '../../Components/Navigation'

export default function Home() {
  const [balance, setBalance] = useState(0)


  useEffect(() => {
    let bal = localStorage.getItem('balance');
    setBalance(bal ? bal : 0)
  }, [])


  return (
    <div className=''>
      <Navigation/>
      <br/>
      <div className="content-wrapper" style={{ minHeight: '450px' }}>
      <section className="content">
        <div className="container-fluid">
          <div className="wrapperevent">
            <div className="eventsHeader">
              <div className="title">Today's Events</div>
              <div className="currentPoints">
                Your Points:
                {Number(balance).toFixed(2)}
              </div>
            </div>
            <div className="events">
              <figure className="event">
                <img src="https://content-file-upload.s3.amazonaws.com/sabong-event.jpg" alt="sabong" />
                <figcaption>
                  <div className="name">Natagan na ba tanan? (SET B)</div>
                  <div className="date">Monday, December 26, 2022</div>
                  <a
                    className="btn btn-outline-warning btn-block"
                    href="/sabong/arena/3799"
                  >
                    ENTER EVENT
                  </a>
                </figcaption>
              </figure>
            </div>
          </div>
        </div>
      </section>
    </div>
    </div>
  )
}
