import React from 'react'
import '../../styles/login.css';
import '../../styles/arena.css';
import '../../styles/events.css';

export default function Login() {
  const handleLogin = (e) => {
    e.preventDefault();
    window.location.href = '/sabong'
  }
  return (
    <div className="text-center">
    <div className="content-wrapper" style={{ minHeight: '387px' }}>
      <section className="content">
        <div className="container-fluid">
          <div className="wrapperevent">
            <div className="col-md-5 col-md-offset-1 col-sm-12 col-xs-12 text-center">
              <div className="card login">
                <div className="card__content">
                <form
            className="form-horizontal text-left"
            onSubmit={handleLogin}
          >
            <input
              name="__RequestVerificationToken"
              type="hidden"
              value="ONN8rIjCzV6B8Krsz7nGCvjXCppn4LAR6YooenoWFkSPofp00-eVL30b4pVkqx9_IxuigTVcbZghULXsB6RjmlKd6b01"
            />
            <div className="error-div small">
              <span
                className="field-validation-valid"
                data-valmsg-for="InvalidLogin"
                data-valmsg-replace="true"
              ></span>
            </div>
            <br />
            <div className="form-group has-success has-feedback ">
              <div className="col-sm-12 ">
                <input
                  className="form-control input input-lg"
                  data-val="true"
                  data-val-required="The Username field is required."
                  id="UserLogin_Username"
                  name="UserLogin.Username"
                  placeholder="enter username"
                  type="text"
                  value=""
                />
                <div className="error-div small">
                  <span
                    className="field-validation-valid text-danger"
                    data-valmsg-for="UserLogin.Username"
                    data-valmsg-replace="true"
                  ></span>
                </div>
              </div>
            </div>
            <div className="form-group  has-success has-feedback">
              <div className="col-sm-12">
                <input
                  className="form-control input input-lg"
                  data-val="true"
                  data-val-required="The Password field is required."
                  id="UserLogin_Password"
                  name="UserLogin.Password"
                  placeholder="enter password"
                  type="password"
                />
                <div className="error-div small">
                  <span
                    className="field-validation-valid text-danger"
                    data-valmsg-for="UserLogin.Password"
                    data-valmsg-replace="true"
                  ></span>
                </div>
              </div>
            </div>
            <div className="form-group">
      <div className="col-sm-12 col-xs-12">
        <button
          type="submit"
          className="btn col-sm-12 col-xs-12 btn-lg btn-warning text-white"
        >
          LOG in
        </button>
      </div>
    </div>
            </form>
                  </div>
                  </div>
                  </div>
                  </div>
                  </div>
          </section>
     </div>
     </div>
  )
}
